<template>
  <div style="background-color: #f5f5f5;">
    <!-- 页面切换动画 -->
    <transition>
      <keep-alive>
        <router-view />
      </keep-alive>
    </transition>
    <!-- 底部导航栏 -->
    <!-- icon 以后找个设计替换 -->
    <div class="placeholder_box"></div>
    <van-tabbar v-model="active" @change="tabbarChange">
      <van-tabbar-item v-for="(item, index) in tabs" :key="index">
        <img slot="icon"  slot-scope="props" :src="props.active ? item.icon.active : item.icon.normal"/>
        {{ $t("messages.tabbar."+ item.name) }}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import * as types from '@/store/mutations'
import {putCache} from "../utils/cache";
export default {
  data() {
    return {
      tabs: [{
        name: 'home',
        icon: {normal:require('../assets/me_icon/1@3x.png'),
               active:require('../assets/me_icon/1-1@3x.png')
        }

      },
      {
        name: 'classifi',
        icon: {normal:require('../assets/me_icon/2@3x.png'),
          active:require('../assets/me_icon/2-1@3x.png')
        }
      },
      {
        name: 'shoppingCart',
        icon:{normal:require('../assets/me_icon/3@3x.png'),
          active:require('../assets/me_icon/3-1@3x.png')
        }
      },
      {
        name: 'personal',
        icon:{normal:require('../assets/me_icon/4@3x.png'),
          active:require('../assets/me_icon/4-1@3x.png')
        }
      }]
    }
  },
  computed: {
    active: {
      get() {
        return this.$store.state.tabbarIndex
      },
      set(newValue) {
        this.$store.commit(types.SET_TABBAR_INDEX,newValue)
      }
    }
  },
    created(){
        //长连接
        this.$communicationCreate()
    },
  mounted() {

  },
  methods: {
    tabbarChange(index) {
      this.$router.push({
        name: this.tabs[index].name
      })
    }
  }
}
</script>
<style scoped>
.van-tabbar{
  height: 100px;
}
.placeholder_box{
  height: 100px;
}
.van-tabbar-item__text{
  color: #333333;
}
.van-tabbar-item--active{
  color:#ED2F26
}
.van-tabbar-item{
  font-size: 20px;
}
</style>
